.about {
    display: grid;
    grid-template-columns: 1fr 8fr 1fr ; /* Deux colonnes de largeur égale */
    gap: 10px;
    place-items: center;

    transform: translateY(-10%);
    opacity: 0;
}

.about.slide{
    transform: translateY(0);
    transition: transform 1.2s ease-in-out, opacity 1.5s ease-in-out;
    opacity: 1;
}

.intro {
    width: 100%;
    display: grid;
    grid-column: span 3;

    grid-template-columns: 1fr 1fr; /* Deux colonnes de largeur égale */
    gap: 10px;
    place-items: center;
}

.texte {
    grid-column: span 2;
}

.about button {
    width: 30vw;
    height: 20vw;

    background-color: var(--primary-color); /* Couleur de fond */
    color: var(--background-color); /* Couleur du texte */
    padding: 10px 5vw; /* Espacement interne */
    text-align: center; /* Alignement du texte */
    text-decoration: none; /* Pas de soulignement */
    display: inline-block; /* Affichage en ligne */
    font-size: 3vw; /* Taille de la police */
    border: solid 3px transparent; /* Pas de bordure */
    border-radius: 10px; /* Coins arrondis */
    transition: background-color 0.3s, transform 0.2s;
}

.about button:hover { 
    transform: scale(1.02); /* Légère augmentation de la taille */
}

button.cv {
    background-color: var(--background-color);
    border-color: var(--primary-color);
    color: var(--primary-color);
    text-align: center;

}

button.cv:hover {
    background-color: var(--primary-color);
    color: var(--background-color);
}

.about .reseaux {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 15px;
    transform: translateX(20px);
    transform: translateY(200%);
    visibility: hidden;
}
.reseaux::after{
    content : '';
    width:2px ;
    height: 40px;
    transform: translateX(13px);
    background-color: var(--primary-color);
}
@keyframes slideY{
    from { visibility:visible;opacity: 0; transform: translateX(20px) translateY(80%); }
    to {  opacity: 1;transform: translateX(20px) translateY(0); }
}

@keyframes slideYscroll{
    from { visibility:visible;opacity: 0; transform: translateX(-20px) translateY(80%); }
    to {  opacity: 1;transform: translateX(-20px) translateY(0); }
}
.about.slide .reseaux{
    transform: translateY(0);
    transform: translateX(20px);
    animation: slideY 3s ease-in-out;
    /*animation-delay: 0.5s;*/
    visibility:visible;
    
}

.about.slide .reseaux.scroll {
    transform: translateX(-20px);
    animation: slideYscroll 3s ease-in-out;
}


.reseau {
    width: 30px;
    height: 30px;
    border-radius: 5px;
    background-color: rgba(var(--rgb-primary-dark),0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.5s;
}
box-icon[type='logo']{
    fill: var(--primary-color);
}
.reseau:hover{
    background-color: var(--primary-color);
}

.reseau:hover box-icon[type='logo'] {
    fill: var(--background-color);
}
.about img {
    width: 120%;
    aspect-ratio: (3/4);
}

.about .image {
    width: 65%;
    aspect-ratio: (3/4);
    margin-top: 10%;
    display: flex;
    justify-content: center;
    /*background-image: linear-gradient(180deg, var(--primary-color) 0%, color-mix(in srgb, var(--primary-color) 0%, transparent) 100%);*/
    background-image: linear-gradient(190deg,var(--primary-color) 0%, rgba(0,0,0,0) 85%);
    border-radius: 9999px 9999px 0% 0%;

    transform: translateY(-10%);
    transition: all 2s ease-in-out;
}
.image::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(to bottom,
                  rgba(0, 0, 0, 0) 0%,
                  rgba(0, 0, 0, 0) 60%,
                  rgba(51,51,51,1) 100%);
    mix-blend-mode:darken;
  }

.about.slide .image {
    transform: translateY(0);
}